@import 'variables';

.menu {
  @include flexbox(column, center, center, nowrap, 20px);
  width: 100%;

  .menu-stock {
    @include flexbox(column, center, center, nowrap, 10px);
    max-width: 1200px;
    width: 100%;
    margin-top: 1.5rem;
  
    #category-buttons {
      @include flexbox(row, flex-start, center, nowrap);
      width: 100%;
      padding: 1.5rem 1rem;
      gap: 10px;
      border: 1.5px solid lightgray;
      border-radius: 4px;
      margin-top: 1rem;

      button {
        border: 1.5px solid lightgray;
      }
    }
  }

  .menu-item-details {
    @include flexbox(column, center, center);
    width: 100%;
    max-width: 600px;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;

    h3 {
      margin-bottom: 20px;
      text-align: center;
    }

    form {
      display: flex;
      flex-direction: column;

      p {
        margin: 0.75rem 0;
        font-size: 14px;
        opacity: .8;
      }

      label {
        margin-top: 10px;
        margin-bottom: 5px;
        opacity: .8;
        font-size: 14px;
      }

      input,
      textarea {
        padding: .75rem .65rem;
        font-size: 14px;
        border: 1px solid #ccc;
        border-radius: 4px;
        margin-bottom: 1.24rem;
        opacity: .8;
      }

      .modifyActions {
        @include flexbox(row, space-between, center);
        width: 100%;
        margin-top: 2rem;

        button:nth-child(1) {
          background-color: rgb(28, 151, 233);
          color: rgb(235, 230, 230);
        }
        button:nth-child(2) {
          background-color: rgb(233, 28, 96);
          color: rgb(235, 230, 230);
        }
      }
    }
  }

  .addMenuItem {
    @include flexbox(column, center, center, nowrap, 20px);
    width: 100%;
    max-width: 600px;
    padding: 2rem 1.5rem;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;

    h3 {
      margin-bottom: 20px;
      text-align: center;
    }

    form {
      display: flex;
      flex-direction: column;
      width: 100%;

      p {
        font-size: 14px;
        margin: 0.65rem 0;
      }

      label {
        margin-top: 10px;
        margin-bottom: 5px;
        font-size: 14px;
        font-weight: 600;
        opacity: .8;
      }

      input,
      textarea {
        border: 1px solid #ccc;
        border-radius: 4px;
        padding: .75rem .65rem;
        font-size: 14px;
        margin-bottom: 1.25rem;
      }

      .addActions {
        @include flexbox(row, space-between, center);
        width: 100%;
        margin-top: 2rem;

        button:nth-child(1) {
          background-color: rgb(28, 151, 233);
          color: rgb(235, 230, 230);
        }

        button:nth-child(2) {
          background-color: rgb(233, 28, 96);
          color: rgb(235, 230, 230);
        }
      }
    }
  }
  
  .hidden {
    display: none;
  }
}